<template>
  <div class="back">
    <!-- 背景音乐 -->
    <audio loop auto ref="audio">
      <source src="../../assets/music/back_music1.mp3">
    </audio>
    <!-- 命中敌机音乐 -->
    <audio ref="bulletShoot">
      <source src="../../assets/music/bullet.mp3">
    </audio>
    <!-- 本机被摧毁音乐 -->
    <audio ref="myPlaneBoom">
      <source src="../../assets/music/boom.mp3">
    </audio>
    <div class="map">
      <div class="myPlane" :style="{left: myPlane.x + 'px', top: myPlane.y + 'px'}"></div>
      <div class="action_menu" v-if="!isPlaying">
        <div class="start_game" @click="startGame">开始游戏</div>
      </div>
    </div>
    <div style="padding: 20px;">
      <div>当前关卡:<span>第{{ num }}关</span></div>
      <div>当前得分:<span>{{ score }}</span></div>
    </div>
  </div>
</template>

<script>
  import boom from '../../assets/boom.png';
  import enemyBoom from '../../assets/boom.png'
  export default {
    data() {
      return {
        // 是否正在进行游戏
        isPlaying: false,
        // 地图高度
        mapHeight: null,
        myPlane: {
          x: 195,
          y: 0,
          width: 60,
          height: 76,
          speed: 12, // 移动速度  
        },
        enemyPlane: {
          x: 0,
          y: 0,
          width: 32,
          height: 23,
          speed: 3000,
        },
        // 敌机
        enemys: [],
        // 子弹
        bullets: [],
        bulletInterval: null,
        enemyPlaneInterval: null,
        mapEle: null,
        // 得分
        score: 0,
        // 通关分数
        passScore: 1000,
        // 关卡
        num: 1,
      }
    },
    mounted() {
      this.mapEle = document.querySelector(".map");
      this.mapHeight = document.getElementsByClassName('map')[0].clientHeight;
      this.myPlane.y = this.mapHeight - 76 - 10;
    },
    beforeDestroy() {
      clearInterval(this.bulletInterval);
      clearInterval(this.enemyPlaneInterval);
    },
    methods: {
      // 添加键盘监听事件
      addKeyboardListen() {
        window.addEventListener('keydown', this.handleKeyDown);
      },
      // 开始游戏
      startGame() {
        this.$refs.audio.currentTime = 0; //从头开始播放提示音
        this.$refs.audio.play(); //播放
        this.addKeyboardListen();
        this.intervalInit();
        this.isPlaying = true;
      },
      // 每隔一段时间创建子弹与敌机
      intervalInit() {
        // 每隔1s创建一颗子弹
        this.bulletInterval = setInterval(() => this.initBullet(), 100);
        // 每隔speed s生成一架敌机
        this.enemyPlaneInterval = setInterval(() => this.initEnemyPlane(), this.enemyPlane.speed);
      },
      handleKeyDown() {
        if (event.key === 'ArrowLeft') {
          this.myPlaneMove('left');
        } else if (event.key === 'ArrowRight') {
          this.myPlaneMove('right');
        } else if (event.key === 'ArrowUp') {
          this.myPlaneMove('up');
        } else if (event.key === 'ArrowDown') {
          this.myPlaneMove('down');
        }
      },
      // 控制飞机移动(键盘左右)
      myPlaneMove(direction) {
        if (!this.myPlane.moving) {
          if (direction === 'left') {
            if (this.myPlane.x <= 10) {
              return;
            }
            this.myPlane.x -= this.myPlane.speed;
          } else if (direction === 'right') {
            if (this.myPlane.x >= 380) {
              return;
            }
            this.myPlane.x += this.myPlane.speed;
          }
          if (direction === 'up') {
            if (this.myPlane.y <= 10) {
              return;
            }
            this.myPlane.y -= this.myPlane.speed;
          } else if (direction === 'down') {
            if (this.myPlane.y >= this.mapHeight - 76 - 10) {
              return;
            }
            this.myPlane.y += this.myPlane.speed;
          }
        }
      },
      // 生成飞机子弹
      initBullet() {
        const bulletEle = document.createElement("div");
        bulletEle.className = "bullet";
        const x = this.myPlane.x + this.myPlane.width / 2 - 1.5;
        var y = this.myPlane.y - 10;
        const duration = Math.random();
        const styleSheets = {
          left: `${x}px`,
          top: `${y}px`,
        };
        Object.assign(bulletEle.style, styleSheets);
        this.mapEle.appendChild(bulletEle);
        // 添加射击动画
        this.shootAnimate(bulletEle, 0, this.mapEle);
      },
      // 添加射击动画
      shootAnimate(obj, rug, mapEle) {
        clearInterval(obj.timer);
        obj.timer = setInterval(() => {
          if (obj.offsetTop + 10 <= rug) {
            clearInterval(obj.timer);
            mapEle.removeChild(obj);
          } else {
            obj.style.top = obj.offsetTop - 14 + 'px';
            this.enemyPlaneBulletCollision();
          }
        }, 50)
      },
      // 生成敌机
      initEnemyPlane() {
        const mapEle = document.querySelector(".map");
        const planeEle = document.createElement("div");
        planeEle.className = "enemyPlane";
        const left = Math.floor(Math.random() * 400);
        const duration = Math.random();
        const styleSheets = {
          left: `${left}px`,
          animationDuration: `${1 + duration}s`,
        };
        Object.assign(planeEle.style, styleSheets);
        this.mapEle.appendChild(planeEle);
        this.enemyPlaneMove(planeEle, this.mapEle.clientHeight, this.mapEle);
      },
      // 生成被摧毁的敌机
      initDesoryedEnemyPlane(enemy) {
        const mapEle = document.querySelector(".map");
        const planeEle = document.createElement("div");
        planeEle.className = "desoryedEnemyPlane";
        const left = Math.floor(Math.random() * 400);
        const duration = Math.random();
        const styleSheets = {
          left: `${enemy.offsetLeft}px`,
          top:`${enemy.offsetTop}px`
        };
        Object.assign(planeEle.style, styleSheets);
        this.mapEle.appendChild(planeEle);
        setTimeout(() => {
          this.mapEle.removeChild(planeEle);
        },200)
      },
      // 敌机移动动画
      enemyPlaneMove(obj, rug, mapEle) {
        clearInterval(obj.timer);
        obj.timer = setInterval(() => {
          if (obj.offsetTop > rug) {
            clearInterval(obj.timer);
            mapEle.removeChild(obj);
          } else {
            obj.style.top = obj.offsetTop + 24 + 'px';
            this.enemyWithMyPlaneCollision();
          }
        }, 100)

      },
      // 敌机与飞机子弹碰撞检测
      enemyPlaneBulletCollision() {
        // 获取所有敌机
        const enemys = document.querySelectorAll(".enemyPlane");
        // 获取所有子弹
        const bullets = document.querySelectorAll(".bullet");
        enemys.forEach((enemy, eIndex) => {
          bullets.forEach((bullet, bIndex) => {
            if (this.isCollision(enemy, bullet)) {
              this.score = this.score + 10;
              this.initDesoryedEnemyPlane(enemy);
              this.mapEle.removeChild(enemy);
              this.mapEle.removeChild(bullet);
              this.$refs.bulletShoot.play();
              setTimeout(() => {
                if (this.score >= this.passScore) {
                  this.stopAnimation();
                  this.enemyPlane.speed -= 100;
                  if (this.enemyPlane.speed <= 200) {
                    this.$alert("恭喜你,通关了!", "系统提示", {
                      confirmButtonText: "重新开始",
                      type: 'success'
                    }).then(() => {
                      this.$router.go(0);
                      this.score = 0;
                    }).catch(() => {});
                  } else {
                    this.$alert("恭喜你,分数达标,进入下一关!", "系统提示", {
                      confirmButtonText: "下一关",
                      type: 'success'
                    }).then(() => {
                      this.score = 0;
                      this.num++;
                      // 每隔1s创建一颗子弹
                      this.bulletInterval = setInterval(() => this.initBullet(), 1000);
                      // 每隔speed s生成一架敌机
                      this.enemyPlaneInterval = setInterval(() => this.initEnemyPlane(), this.enemyPlane
                        .speed);
                    }).catch(() => {});
                  }
                }
              });
            }
          });
        });
      },
      fun(i) {
        return function() {

        }
      },
      // 敌机与我方飞机碰撞检测
      enemyWithMyPlaneCollision() {
        // 获取所有敌机
        const enemys = document.querySelectorAll(".enemyPlane");
        const myPlane = document.querySelector(".myPlane");
        for (var i = 0; i < enemys.length; i++) {
          let enemy = enemys[i];
          if (this.isCollision(enemy, myPlane)) {
            this.$refs.myPlaneBoom.play();
            this.$refs.audio.pause();
            myPlane.style.backgroundImage = `url(${boom})`;
            this.stopAnimation();
            this.$alert("对不起,您的飞机爆炸了!", "提示", {
              confirmButtonText: "确定",
              type: 'warning'
            }).then(() => {
              this.$router.go(0);
            }).catch(() => {});
          }
        }
      },
      // 碰撞检测
      isCollision(objA, objB) {
        let collision = objA.offsetLeft + objA.offsetWidth >= objB.offsetLeft &&
          objB.offsetLeft + objB.offsetWidth >= objA.offsetLeft &&
          objA.offsetTop + objA.offsetHeight >= objB.offsetTop &&
          objB.offsetTop + objB.offsetHeight >= objA.offsetTop;
        return collision;
      },
      // 停止动画,并移除所有敌机与子弹
      stopAnimation() {
        // 获取所有敌机
        const enemyPlanes = document.querySelectorAll(".enemyPlane");
        // 获取所有子弹
        const bullets = document.querySelectorAll(".bullet");
        enemyPlanes.forEach((enemyPlane) => {
          if (enemyPlane) {
            clearInterval(enemyPlane.timer);
            this.mapEle.removeChild(enemyPlane);
          }
        });
        bullets.forEach((bullet) => {
          if (bullet) {
            clearInterval(bullet.timer);
            this.mapEle.removeChild(bullet);
          }
        });
        clearInterval(this.bulletInterval);
        clearInterval(this.enemyPlaneInterval);
      }
    }
  }
</script>

<style>
  .action_menu {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #00000066;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .start_game {
    width: 50%;
    height: 50px;
    line-height: 50px;
    border-radius: 25px;
    background-color: white;
    text-align: center;
    cursor: pointer;
  }

  .back {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  .map {
    width: 450px;
    height: 100%;
    background-image: url('../../assets/map.png');
    background-size: cover;
    position: relative;
  }

  .myPlane {
    position: absolute;
    background-image: url('../../assets/my_plane.png');
    background-size: cover;
    width: 60px;
    height: 76px;
  }

  .enemyPlane {
    position: absolute;
    background-image: url('../../assets/enemy_plane.png');
    background-size: cover;
    width: 32px;
    height: 23px;
  }

  .desoryedEnemyPlane {
    position: absolute;
    background-image: url('../../assets/enemyBoom.png');
    background-size: cover;
    width: 32px;
    height: 23px;
  }

  .bullet {
    position: absolute;
    background-image: url('../../assets/bullet.png');
    background-size: cover;
    width: 6px;
    height: 10px;
  }
</style>
